.buttonPrime {
    text-transform: uppercase;
    cursor: pointer;
    color: #fff;
    /* height: 40px; */
    padding: 8px 16px 8px 16px;
    opacity: 1px;
    /* font-size: 16px; */
    font-size: clamp(12px, 3vw, 16px);
    font-weight: 500;
    margin: 0 0 0 1rem;
}

.none {
    background: transparent;
    border: none;
    margin: 0;
    padding: 0;
    align-self: center;
    display: inline-flex;
    text-transform: none;
    font-size: inherit;
}

.wallet {
    background: linear-gradient(180deg, #FBAE56 0%, #F28100 100%);
    box-shadow: 0px 0px 4.1px 0px #FFFFFF inset;
    width: 108px;
    border-radius: 8px;
    border: none;
    text-shadow: 0px 1px #00000026;
    margin: 0;
}

.walletMobile {
    width: 100% !important;
    padding: 0;
    margin: 0;
}

.signIn {
    background: linear-gradient(180deg, rgba(217, 230, 255, 0.25) 0%, rgba(212, 226, 255, 0.0775) 100%);
    height: 40px;
    width: 88px;
    gap: 10px;
    border-radius: 12px;
    text-shadow: 0px 1px #00000026;
    border: 1px solid #747A89;
    margin: 0;
    padding: 0;
    font-size: 1em;
}

.singUp {
    background: linear-gradient(180deg, #FBAE56 0%, #F28100 100%);
    box-shadow: 0px 0px 4.1px 0px #FFFFFF inset;
    width: 108px;
    height: 40px;
    gap: 8px;
    border: none;
    border-radius: 12px;
    padding: 0;
    margin: 0;
}

.checkNow {
    min-width: 152px;
    height: 40px;
    padding: 8px 16px 8px 16px;
    border-radius: 12px;
    opacity: 1px;
    background: linear-gradient(180deg, #7C88A6 0%, rgba(108, 119, 145, 0.33) 100%);
    border-image-source: linear-gradient(118.5deg, rgba(255, 255, 255, 0.3) -5.98%, rgba(255, 255, 255, 0) 139.12%);
    box-shadow: 0px 4px 14px 0px #00000026;
    border: none;
    font-family: var(--fontFamilyNunito);
    margin: 0;
    font-size: 1em;
    font-weight: 600;
}

.seeDoc {
    border: none;
    background: transparent;
    margin: 0;
    padding: 0;
    text-transform: capitalize;
}

.activeTab {
    width: -webkit-fill-available;
    max-width: 364.83px;
    height: 100%;
    padding: 8px;
    gap: 8px;
    border-radius: 12px;
    opacity: 1px;
    background: linear-gradient(180deg, #415266 0%, #303A52 100%);
    border: none;
    margin: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    font-family: var(--fontFamilyNunito);
    font-weight: 600;
    font-size: clamp(12px, 4vw, 14px);
}

.inactiveTab {
    width: -webkit-fill-available;
    max-width: 364.83px;
    /* min-height: 40px; */
    height: 100%;
    padding: 8px;
    gap: 8px;
    border-radius: 8px;
    opacity: 1px;
    background: #1C2939;
    border: none;
    margin: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    font-family: var(--fontFamilyNunito);
    font-weight: 600;
    font-size: clamp(12px, 4vw, 14px);
}

@media screen and (max-width: 426px) {
    .inactiveTab {
        height: 60px;
    }

    .activeTab {
        height: 60px;
    }
}

@media screen and (max-width: 390px) {
    .inactiveTab {
        height: 40px;
    }

    .activeTab {
        height: 40px;
    }
}

