.authContinue {
    max-width: 380px;
    max-height: 40px;
    width: 100%;
    padding: 2% 4%;
    gap: 8px;
    border-radius: 12px;
    background: linear-gradient(180deg, #FBAE56 0%, #F28100 100%);
    box-shadow: 0px 0px 4.1px 0px #FFFFFF inset;
    margin: 0 !important;
    border: none;
}

.profileButton {
    border: none;
    background: none;
    margin: 0;
    padding: 0;
}

.activate {
    width: 106px;
    height: 40px;
    gap: 10px;
    border-radius: 12px;
    border: 1px;
    border: 1px solid #747A89;
    background: linear-gradient(180deg, rgba(217, 230, 255, 0.25) 0%, rgba(212, 226, 255, 0.0775) 100%);
    padding: 0;
    margin: 0;
    cursor: pointer;
}

.understood {
    width: 140px;
    height: 40px;
    gap: 10px;
    border-radius: 12px;
    border: 1px solid #747A89;
    background: linear-gradient(180deg, rgba(217, 230, 255, 0.25) 0%, rgba(212, 226, 255, 0.0775) 100%);
    padding: 0;
    margin: 0;
    cursor: pointer;
}

.crossButton {
    margin: 0;
    padding: 0;
    background: none;
    border: none;
}

.inputSliderBox {
    background: #293B65;
    max-width: 36px;
    max-height: 36px;
    padding: 3%;
    gap: 8px;
    border-radius: 8px;
    margin: 0;
    border: none;
    width: 100%;
    align-items: center;
    display: flex;
    justify-content: center;
}

.headingGradient-btnblue {
    background: linear-gradient(180deg, #5B82AE 0%, #224268 100%);
    box-shadow: 0px 0px 5.5px 0px #FFFFFF4D inset;
    width: 100%;
    max-height: 68px;
    height: 100%;
    gap: var(--spacing0);
    border-radius: 8px;
    border: none;
    padding: 0 !important;
    margin: 0 !important;
    aspect-ratio: 224/68;
}

.headingGradient-btnnone {
    width: 100%;
    max-height: 68px;
    height: 100%;
    gap: var(--spacing0);
    background: none;
    border: none;
    padding: 0 !important;
    margin: 0 !important;
    aspect-ratio: 224/68;
}

.diceGameInc {
    border-right: 2px solid #1C334E;
    width: 48px;
    height: 100%;
    gap: 8px;
    padding: 0;
    margin: 0;
    background: #061B33;
    border: transparent;
    border-top-right-radius: 8px;
    border-bottom-right-radius: 8px;
    border-left: 2px solid #374151;
}

.diceGameDec {
    width: 48px;
    gap: 8px;
    padding: 0;
    margin: 0;
    background: #061B33;
    border: none;
    border-top-left-radius: 8px;
    border-bottom-left-radius: 8px;
    border-right: 2px solid #374151;
}

@media screen and (max-height: 769px) {
    .inputSliderBox {
        align-items: center;
        display: flex;
        justify-content: center;
        height: 26px;
        width: 26px;
        padding: 3%;
    }

    .inputSliderBox img {
        padding: .1rem;
        align-items: center;
        display: flex;
        justify-content: center;
    }

}

.betDetailsPlayNow {
    /* width: 117px; */
    width: clamp(100px, 20vw, 117px);
    /* height: 40px; */
    height: clamp(30px, 5.5vh, 40px);
    gap: 8px;
    border-radius: 12px;
    background: linear-gradient(180deg, #FBAE56 0%, #F28100 100%);
    box-shadow: 0px 0px 4.1px 0px #FFFFFF inset;
    font-family: var(--fontFamilyFredoka);
    font-size: clamp(14px, 2vw, 16px);
    font-weight: 500;
    line-height: 24px;
    letter-spacing: 0.02em;
    text-align: center;
    padding: 0;
    margin: 0;
    border: transparent;
}

.gamePlay {
    height: clamp(30px, 5.5vh, 40px)
}

.startPlaying {
    background: linear-gradient(180deg, #FBAE56 0%, #F28100 100%);
    box-shadow: 0px 0px 4.1px 0px #FFFFFF inset;
    min-width: 172px;
    min-height: 40px;
    gap: 8px;
    border-radius: 8px;
    padding: 0;
    margin: 0;
    border: transparent;
}


