.contentBody {
    top: 120px;
    margin-bottom: 120px;
    position: relative;
    /* max-width: 1118.5px; */
    gap: 3rem;
    /* display: flex; */
    padding: 0 6rem;
    display: flex;
    justify-content: center;
}

@media screen and (max-width: 992px) {
    .contentBody {
        padding: 0 1rem;
    }
}

@media screen and (max-width: 426px) {
    .contentBody {
        top: 120px;
        position: relative;
    }
}

