.dice {
    display: flex;
    flex-direction: column;
    gap: 24px;
    padding: 7.5% 4.5%;
}


.diceContainer {
    position: absolute;
    top: 70%;
    left: 50%;
    -ms-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
}

.mainCard {
    /* padding: 32px 24px 32px 24px; */
    background: #122D4B;
    border: 2px solid #68748F;
    max-height: 701px;
    max-width: fit-content;
    height: 80%;
    width: 100%;
    aspect-ratio: 508/701;
    /* gap: 3%; */
}


.numberLogo {
    /* width: 18.75rem; */
    /* max-height: 5rem; */
    height: 14%;
    position: absolute;
    top: -8%;
    display: flex;
    align-self: center;
}

.ProgressBarmeter {
    display: flex;
    color: #bbb;
    justify-content: space-between
}

/* Progress bar */
.progressContainer {
    padding: 0 3%;
}

.progress {
    max-width: 412px;
    height: 8px;
    width: 100%;
}

.progress {
    margin: 0 5px;
    box-shadow: inset 0 0 4px #000000;
    /* --bs-progress-height: 0.6rem; */
    --bs-progress-height: 31%;
}

.progressUnder {
    --bs-progress-bar-bg: #61d05c;
    --bs-progress-bg: #eb5128;
}

.progressOver {
    --bs-progress-bar-bg: #61d05c !important;
    --bs-progress-bg: #eb5128 !important;
}

.group2 {
    display: flex;
    gap: 22px;
    flex-direction: column;
}

@media screen and (max-height: 840px) {
    .group2 {
        gap: calc(22px - 10px);
    }
}

@media screen and (max-height: 793px) {
    .dice {
        gap: 3%
    }

    .mainCard {
        height: 80%;
