.inputSliderDiv {
    display: flex;
    align-items: center;
}

.headingRoll {
    background-image: linear-gradient(to right, #FFD809 0%, #FF8A00 100%);
    /* font-size: 5.14286em; */
    font-size: clamp(1rem, 1.1505rem + 5.7184vh, 4.5rem);
    font-weight: 400;
    text-align: center;
    font-family: var(--fontFamilyFredokaOne);
    width: 100%;
    -webkit-background-clip: text;
    background-clip: text;
    color: transparent;
    padding: 0;
    max-height: 86px;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}


/* BET HISTORY */

.bet-content {
    font-family: var(--fontFamilyFredoka);
    font-size: var(--fontSize18);
    font-weight: 400;
    line-height: 24px;
    text-align: center;
}

.bets-history {
    /* margin: 0 auto; */
    width: 100%;
    overflow: hidden;
    text-align: right;
    direction: rtl;
    /* max-width: 436px; */
    display: flex;
    max-height: 36px;
    /* height: 100%; */
    gap: 1.25rem
}

.individual-bet {
    gap: 8px;
    border-radius: 8px;
    border: 1px;
    text-decoration: none;
    max-width: 4.5rem;
    direction: rtl;
    max-height: 2.25rem;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: clamp(8px, 2.5vh, 18px);
    cursor: pointer;
    padding: 1% 5%;
}

.bets-history .individual-bet span {
    opacity: 50%;
}

/* Chance */
.card-text {
    box-shadow: 0px -0.5px 2.5px 0.5px #FFFFFF1A;
    background: #061B33;
    padding: 2.4% 1.7%;
    border-radius: 8px;
    height: 11.37%;
    align-items: center;
}

.chanceTxt {
    font-family: var(--fontFamilyFredoka);
    font-size: var(--fontSize14h);
    font-weight: 500;
    line-height: 20px;
    letter-spacing: 0.02em;
    text-align: left;
    color: #9CA3AF;
}

.chanceWin {
    font-family: var(--fontFamilyFredokaOne);
    font-size: var(--fontSize18h);
    font-weight: 400;
    line-height: 24px;
    text-align: left;
    color: #F9FAFB;
    opacity: 70%;
}

.group4 {
    max-height: 109px;
    background: #061B33;
    box-shadow: 0px -0.5px 2.5px 0.5px #FFFFFF1A;
    padding: 2.614%;
    gap: 12px;
    border-radius: 8px;
    border: 2px 2px 0px 2px;
    display: flex;
    flex-direction: column;
    height: 28.2%;
}

.targetPayout {
    font-family: var(--fontFamilyFredoka);
    font-size: var(--fontSize14h);
    font-weight: 500;
    line-height: 20px;
    letter-spacing: 0.02em;
    text-align: left;
    color: #9CA3AF;
}

.targetPayoutValue {
    font-family: var(--fontFamilyFredoka);
    font-size: var(--fontSize18h);
    font-weight: 600;
    line-height: 24px;
    text-align: left;
    color: #FFFFFF;
}

.bottomValue {
    font-family: var(--fontFamilyFredokaOne);
    font-size: var(--fontSize20h);
    font-weight: 400;
    line-height: 24px;
    text-align: center;
    background: #061B33;
    width: 100%;
    color: #ffffffc7;
    border: none;
    border-color: transparent;
    outline: none;
    height: 100%;
}

.betValueDiv {
    background: #061B33;
}

.bottomGroup {
    height: 10%;
    background: #061B33;
    box-shadow: 0px -0.5px 2.5px 0.5px #FFFFFF1A;
    border-radius: 8px;
}

.guessTxt {
    font-family: var(--fontFamilyFredokaOne);
    font-size: var(--fontSize18h);
    font-weight: 400;
    line-height: 28px;
    letter-spacing: 0em;
}

.guessMux {
    font-family: var(--fontFamilyNunito);
    font-size: var(--fontSize14h);
    font-weight: 700;
    line-height: 20px;
    letter-spacing: 0em;
}

.progressBarNum {
    font-family: var(--fontFamilyNunito);
    font-size: var(--fontSize14h);
    font-weight: 500;
    line-height: 20px;
    text-align: center;
    color: #FFFFFF;
    opacity: 40%;
}

@media screen and (max-height: 570px) {
    .dice {
        gap: 2%;
    }

    .mainCard {
        height: 70%;
    }

    .bets-history {
        max-height: 1.3rem !important;
    }

    .individual-bet {
        font-size: clamp(8px, 2vh, 18px) !important;
    }

    .headingRoll {
        height: 60% !important;
    }

    .progressBarNum {
        font-size: clamp(8px, 2vh, 14px);

    }

    .group2 {
        margin-bottom: 0rem !important;
    }
}

@media screen and (min-height: 500px) {
    .bets-history {
        margin-top: 2%;
    }
}

