}

.headingGradient {
    background-image: linear-gradient(to right, #FFE30B 0%, #FFA627 100%);
    -webkit-background-clip: text;
    background-clip: text;
    color: transparent;
}

.headingGradient2 {
    background-image: linear-gradient(to right, #FFD809 0%, #FF8A00 100%);
    -webkit-background-clip: text;
    background-clip: text;
    color: transparent;
}

.btn:hover {
